/*********** PopUp - Anvil ***********/
#popup-container {
  width: 100%;
}

#overlay {
  position: fixed;
  opacity: 0;
  top: 0;
  bottom: 0;
  background:rgba(254, 254, 254, 0.85);
  width: 100%;
  height: 100%;
  z-index: 99999;
  visibility: hidden;
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}
#popup {
  position: fixed;
  top: 35%;
  left: 55%;
  /*
  top: 50%;
  left: 50%;
  background: #fff;
  width: 500px;
  height: 500px;
  */
  margin-left: -250px; /*Half the value of width to center div*/
  margin-top: -250px; /*Half the value of height to center div*/
  z-index: 999999;
  visibility: hidden;
}
#popupclose {
  position: absolute;
  float: right;
  right: 0px;
  padding: 10px;
  cursor: pointer;
  z-index: 1;
}
#popupclose:focus, #popupclose:focus {
  color: orangered;
}
.popupcontent {
  padding: 10px;
}
#button {
  cursor: pointer;
}
@-webkit-keyframes pop-swirl {
0% {
-webkit-transform: scale(0) rotate(360deg);
        transform: scale(0) rotate(360deg);
}

60% {
-webkit-transform: scale(0.8) rotate(-10deg);
        transform: scale(0.8) rotate(-10deg);
}

100% {
-webkit-transform: scale(1) rotate(0deg);
        transform: scale(1) rotate(0deg);
}
}
@keyframes pop-swirl {
0% {
-webkit-transform: scale(0) rotate(360deg);
        transform: scale(0) rotate(360deg);
}

60% {
-webkit-transform: scale(0.8) rotate(-10deg);
        transform: scale(0.8) rotate(-10deg);
}

100% {
-webkit-transform: scale(1) rotate(0deg);
        transform: scale(1) rotate(0deg);
}
}
@-webkit-keyframes anvil {
0% {
-webkit-transform: scale(5) rotate(0);
        transform: scale(5) rotate(0);
opacity: 0;
box-shadow: 0 0 0 rgba(241, 241, 241, 0);
}

50% {
-webkit-transform: scale(1) rotate(-0.2deg);
        transform: scale(1) rotate(-0.2deg);
opacity: 1;
box-shadow: 0 0 0 rgba(241, 241, 241, 0.5);
}

75% {
-webkit-transform: scale(1) rotate(0.2deg);
        transform: scale(1) rotate(0.2deg);
opacity: 1;
box-shadow: 0 0 250px rgba(241, 241, 241, 0.5);
}

100% {
-webkit-transform: scale(1) rotate(0);
        transform: scale(1) rotate(0);
opacity: 1;
box-shadow: 0 0 500px rgba(241, 241, 241, 0);
}
}
@keyframes anvil {
0% {
-webkit-transform: scale(5) rotate(0);
        transform: scale(5) rotate(0);
opacity: 0;
box-shadow: 0 0 0 rgba(241, 241, 241, 0);
}

50% {
-webkit-transform: scale(1) rotate(-0.2deg);
        transform: scale(1) rotate(-0.2deg);
opacity: 1;
box-shadow: 0 0 0 rgba(241, 241, 241, 0.5);
}

75% {
-webkit-transform: scale(1) rotate(0.2deg);
        transform: scale(1) rotate(0.2deg);
opacity: 1;
box-shadow: 0 0 250px rgba(241, 241, 241, 0.5);
}

100% {
-webkit-transform: scale(1) rotate(0);
        transform: scale(1) rotate(0);
opacity: 1;
box-shadow: 0 0 500px rgba(241, 241, 241, 0);
}
}
#popup[data-pop="slide-down"] {
top: -50%;
-webkit-transition: all .5s ease-in-out;
transition: all .5s ease-in-out;
}
#popup[data-pop="slide-down"].show {
visibility: visible;
z-index: 200;
opacity: 1;
top: 50%;
}
#popup[data-pop="slide-down"].show ~ #overlay {
opacity: 1;
visibility: visible;
z-index: 100;
}
#popup[data-pop="pop-in"] {
-webkit-transform: scale(0);
        transform: scale(0);
-webkit-transition: all .5s ease-in-out;
transition: all .5s ease-in-out;
}
#popup[data-pop="pop-in"].show {
-webkit-transform: scale(1);
        transform: scale(1);
visibility: visible;
z-index: 200;
opacity: 1;
}
#popup[data-pop="pop-in"].show ~ #overlay {
opacity: 1;
visibility: visible;
z-index: 100;
}
#popup[data-pop="pop-swirl"] {
-webkit-transform: scale(0);
        transform: scale(0);
-webkit-transition: all .5s ease-in-out;
transition: all .5s ease-in-out;
}
#popup[data-pop="pop-swirl"].show {
-webkit-animation: pop-swirl 1s cubic-bezier(0.38, 0.1, 0.36, 0.9) forwards;
visibility: visible;
z-index: 200;
opacity: 1;
}
#popup[data-pop="pop-swirl"].show ~ #overlay {
opacity: 1;
visibility: visible;
z-index: 100;
}
#popup[data-pop="anvil"] {
-webkit-transition: all .5s ease-in-out;
transition: all .5s ease-in-out;
}
#popup[data-pop="anvil"].show {
-webkit-animation: anvil 1s cubic-bezier(0.38, 0.1, 0.36, 0.9) forwards;
visibility: visible;
z-index: 200;
opacity: 1;
}
#popup[data-pop="anvil"].show ~ #overlay {
opacity: 1;
visibility: visible;
z-index: 100;
}

/*********** CARD ***********/

#popup-container {
  box-sizing: border-box;
  line-height: 1.5;
  font-family: "Open Sans", sans-serif;
}

#popup-container img {
  max-width: 100%;
}

#popup-container .container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: #444;
}

#popup-container .card {
  position: relative;
  margin: auto 0;
  left: 10%;
  background: #333;
  width: 80%;
  max-width: 400px;
  height: auto;
  border-radius: 6px;
  padding: 2rem;
  color: #aaa;
  box-shadow: 0 0.25rem 0.25rem rgba(0, 0, 0, 0.2), 0 0 1rem rgba(0, 0, 0, 0.2);
  overflow: hidden;
}
#popup-container .card__image-container {
  margin: -2rem -2rem 1rem -2rem;
}
#popup-container .card__line {
  opacity: 0;
  -webkit-animation: LineFadeIn 0.8s 0.8s forwards ease-in;
          animation: LineFadeIn 0.8s 0.8s forwards ease-in;
}
#popup-container .card__image {
  opacity: 0;
  -webkit-animation: ImageFadeIn 0.8s 1.4s forwards;
          animation: ImageFadeIn 0.8s 1.4s forwards;
}
#popup-container .card__title {
  color: white;
  margin-top: 0;
  font-weight: 800;
  letter-spacing: 0.01em;
}
#popup-container .card__content {
  margin-top: -1rem;
  opacity: 0;
  -webkit-animation: ContentFadeIn 0.8s 1.6s forwards;
          animation: ContentFadeIn 0.8s 1.6s forwards;
}
#popup-container .card__svg {
  position: absolute;
  left: 0;
  /*top: 115px;*/
  top: 77px;
}

@media screen and (max-width: 480px) {
  #popup {
    margin: 0;
    bottom: 0;
    left: 0;
    top: 0;
  }
}

@-webkit-keyframes LineFadeIn {
  0% {
    opacity: 0;
    d: path("M 0 300 Q 0 300 0 300 Q 0 300 0 300 C 0 300 0 300 0 300 Q 0 300 0 300 ");
    stroke: #fff;
  }
  50% {
    opacity: 1;
    d: path("M 0 300 Q 50 300 100 300 Q 250 300 350 300 C 350 300 500 300 650 300 Q 750 300 800 300");
    stroke: #888bff;
  }
  100% {
    opacity: 1;
    d: path("M -2 100 Q 50 200 100 250 Q 250 400 350 300 C 400 250 550 150 650 300 Q 750 450 802 400");
    stroke: #545581;
  }
}

@keyframes LineFadeIn {
  0% {
    opacity: 0;
    d: path("M 0 300 Q 0 300 0 300 Q 0 300 0 300 C 0 300 0 300 0 300 Q 0 300 0 300 ");
    stroke: #fff;
  }
  50% {
    opacity: 1;
    d: path("M 0 300 Q 50 300 100 300 Q 250 300 350 300 C 350 300 500 300 650 300 Q 750 300 800 300");
    stroke: #888bff;
  }
  100% {
    opacity: 1;
    d: path("M -2 100 Q 50 200 100 250 Q 250 400 350 300 C 400 250 550 150 650 300 Q 750 450 802 400");
    stroke: #545581;
  }
}
@-webkit-keyframes ContentFadeIn {
  0% {
    -webkit-transform: translateY(-1rem);
            transform: translateY(-1rem);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}
@keyframes ContentFadeIn {
  0% {
    -webkit-transform: translateY(-1rem);
            transform: translateY(-1rem);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}
@-webkit-keyframes ImageFadeIn {
  0% {
    -webkit-transform: translate(-0.5rem, -0.5rem) scale(1.05);
            transform: translate(-0.5rem, -0.5rem) scale(1.05);
    opacity: 0;
    -webkit-filter: blur(2px);
            filter: blur(2px);
  }
  50% {
    opacity: 1;
    -webkit-filter: blur(2px);
            filter: blur(2px);
  }
  100% {
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
    opacity: 1;
    -webkit-filter: blur(0);
            filter: blur(0);
  }
}
@keyframes ImageFadeIn {
  0% {
    -webkit-transform: translate(-0.5rem, -0.5rem) scale(1.05);
            transform: translate(-0.5rem, -0.5rem) scale(1.05);
    opacity: 0;
    -webkit-filter: blur(2px);
            filter: blur(2px);
  }
  50% {
    opacity: 1;
